/* src/components/Product.css */

.product {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .product-image {
    width: 100px; /* Adjust image size as needed */
    height: auto;
    margin-right: 20px;
    border-radius: 5px;
  }
  
  .product-details {
    flex-grow: 1;
  }
  
  .product-details h3 {
    margin-top: 0;
  }
  
  .product-details p {
    margin: 5px 0;
  }
  
  .product-details button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .product-details button:hover {
    background-color: #0056b3;
  }
  